<template>
  <div class="activity mescroll">
    <div class="top">
      <div class="top-content">本活动由{{this.detail.shopName}}提供</div>
      <img class="music" src="../../assets/img/activity/music.png" :style="play?'':'animation-play-state:paused'" alt @click="clickAudio" />
      <audio ref="audio" :src="$utils.dealImg(detail.amusic)" autoplay loop></audio>
    </div>
    <div class="mid" id="mid">
      <div class="mid-top">
        <div class="activeInfo">
          <img :src="$utils.dealImg(detail.aimg)" alt />
          <div class="flex">
            <div class="left">
              <span class="symbol">￥</span>
              <span class="price">{{detail.aoutPrice?detail.aoutPrice.toFixed(2):'0.00'}}</span>
              <span class="text">原价{{detail.amarketPrice}}元，限量{{detail.acounts}}件</span>
            </div>
            <div class="right">
              {{detail.avisitnumText}}人关注
            </div>
          </div>
          <div class="name">{{detail.viewName}}</div>
          <div class="flex borderTop">
            <div class="left viewname">{{detail.viewName2}}</div>
            <!-- <div class="right remark">48小时发货，全场包邮</div> -->
          </div>
        </div>
        <div class="time-wrap">
          <p>活动倒计时</p>
          <div class="time">
            <div class="num">
              <div class="value">{{day}}</div>
              <div class="label">
                天
              </div>
            </div>
            <div class="point">:</div>
            <div class="num">
              <div class="value">{{hour}}</div>
              <div class="label">
                时
              </div>
            </div>
            <div class="point">:</div>
            <div class="num">
              <div class="value">{{min}}</div>
              <div class="label">
                分
              </div>
            </div>
            <div class="point">:</div>
            <div class="num">
              <div class="value">{{sec}}</div>
              <div class="label">
                秒
              </div>
            </div>
          </div>
        </div>
        <div class="line-wrap">
          <div class="line"></div>
          <div class="showLine" :style="'width:' +(detail.asalenum/detail.acounts*100) + '%;'"></div>
          <div class="start">0</div>
          <div class="end">{{detail.acounts}}</div>
          <div v-if="detail.asalenum != 0 && detail.asalenum !=detail.acounts" class="now" :style="'left:' +(detail.asalenum/detail.acounts*100) + '%;'">{{detail.asalenum}}</div>
        </div>
      </div>
      <div class="mid-redPacket" id="rank">
        <div class="info">
          <div class="title">
            <p>{{detail.shopName}}</p>
          </div>
          <div class="labels">
            <div class="label" v-for="(item,index) in shopDetail.shopTitles" :key="index" v-if="item">{{item}}</div>
            <div class="label color">{{shopDetail.shopLevelName}}</div>
          </div>
          <div class="flex">
            <div class="list">
              <div class="item">
                <p class="left">电话：</p>
                <p class="right">{{shopDetail.shopMobile}}</p>
              </div>
              <div class="item">
                <p class="left">地址：</p>
                <p class="right">{{(shopDetail.area?shopDetail.area.treeNames?shopDetail.area.treeNames.replace(/\//g,''):'':'') + shopDetail.shopAddr}}</p>
              </div>
              <div class="item">
                <p class="left">营业时间：</p>
                <p class="right">{{shopDetail.shopYingye}}</p>
              </div>
            </div>
          </div>
        </div>
        <div v-if="rankShow">
          <div class="classify">
            <div class="classify-left" @click="choose(1)">
              <img :src="active == 1?require('../../assets/img/activity/zuo2.png'):require('../../assets/img/activity/zuo.png')" alt="">
              <p :style="active == 1?'color:#fff':'color:#000'">{{redPacketetTotal[1]||'0'}}人，共获得{{redPacketetTotal[0]||'0'}}元佣金</p>
            </div>
            <div class="classify-right" @click="choose(2)">
              <img :src="active == 2?require('../../assets/img/activity/you2.png'):require('../../assets/img/activity/you.png')" alt="">
              <p :style="active == 2?'color:#fff':'color:#000'">佣金规则</p>
            </div>
          </div>
          <div class="redPacket-list" v-if="active == 1">
            <div class="redPacket-item" v-for="(item,index) in  redPackList" :key="index">
              <div class="left">
                <img :src="$utils.dealImg(item.userHeader)" alt />
              </div>
              <div class="name">
                <p>{{item.userName}}</p>
              </div>
              <div class="right">
                <span>共获得佣金</span>
                <span class="price">{{item.tmoney}}</span>
                <span>元</span>
              </div>
            </div>
          </div>
          <div class="redPacket-rules" v-if="active == 2">
            <div class="rules-list">
              <div class="rules-item" v-for="(item,index) in detail.aredMsgList" :key="index">{{item}}</div>
            </div>
          </div>
        </div>

      </div>
      <div class="content">
        <p class="title">
          <span style="color:#e4e4e4;margin-right:3px;">————</span>
          <span>活动详情</span>
          <span style="color:#e4e4e4;margin-left:3px;">————</span>
        </p>
        <div v-html="detail.amsg">{{detail.amsg}}</div>
      </div>
      <!--
      <div class="technical" v-if="$config.activeTitle == '爆款王'">
        <img class="logo" src="../../assets/img/activity/logo.png" alt="">
        <p class="title">本活动由“爆款王”提供技术支持</p>
        <div class="flex" @click="add">
          <img class="icon" src="../../assets/img/activity/icon.png" alt="">
          <p class="text">
            我也要推爆款
            <span>》》</span>
          </p>
        </div>
      </div>-->
    </div>
    <div class="bottom">
      <div class="telno" @click="call">
        <img src="../../assets/img/activity/telno.png" alt />
        <p>客服电话</p>
      </div>
      <div class="rank" @click="rank">
        <img src="../../assets/img/activity/ranking.png" alt />
        <p>佣金排行</p>
      </div>
      <div class="btn buy" @click="buy">
        <p>{{detail.aoutPrice}}元</p>
        <p>立即抢购</p>
      </div>
      <div class="btn" @click="shareClick">立即分享</div>
    </div>
    <div class="fixed">
      <div class="pic" @click="toTop">
        <img src="../../assets/img/activity/toTop.png" alt="">
      </div>
      <div class="pic" @click="collect">
        <img src="../../assets/img/activity/collect.png" alt="">
      </div>
      <div class="pic" @click="toMy">
        <img src="../../assets/img/activity/my.png" alt="">
      </div>
    </div>
    <div class="mask" v-if="shareShow" @click="hideShareMask">
      <img src="../../assets/img/activity/shang.png" alt="">
    </div>
    <van-popup v-model="show">
      <div class="popupp-mid">
        <van-icon name="cross" class="close" @click="close" />
        <p class="title">{{$config.activeTitle}}</p>
        <p class="text">请关注{{$config.activeTitle}}微信公众号，收取最新佣金信息</p>
        <img class="qrCode" :src="$config.qrCode" alt="">
        <p class="prompt">扫描上方二维码关注公众号</p>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { getShareActivity, wxApi, activityCollect } from "@/api";
import { Popup, Icon } from "vant";
export default {
  components: {
    [Popup.name]: Popup,
    [Icon.name]: Icon,
  },
  data() {
    return {
      play: true,
      day: "0",
      hour: "00",
      min: "00",
      sec: "00",
      Interval: null,
      active: 1,
      detail: {},
      shopDetail: {},
      redPackList: [],
      redPacketetTotal: [],
      shareShow: false,
      show: false,
      rankShow: false,
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    call() {
      window.location.href = "tel:" + this.detail.aredTel;
    },
    rank() {
      this.active = 1;
      this.rankShow = !this.rankShow;
      rank.scrollIntoView({
        behavior: "smooth",
      });
    },
    close() {
      this.show = false;
    },
    toTop() {
      mid.scrollIntoView({
        block: "start",
        behavior: "smooth",
      });
    },
    toMy() {
      this.$router.push({ name: "activityUser" });
    },
    collect() {
      this.$toast.allowMultiple();
      let loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      activityCollect({
        acode: this.detail.acode,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast("操作成功");
        } else {
          this.$toast(res.msg);
        }
      });
    },
    add() {
      this.$router.push({ name: "addActivity" });
    },
    buy() {
      if (this.detail.astatus == 1) {
        this.$toast("该活动还未开始");
        return;
      }
      if (this.detail.astatus == 2) {
        this.$toast("该活动已结束");
        return;
      }
      if (this.detail.astatus == 3) {
        this.$toast("此活动礼包已售完");
        return;
      }
      // if (this.detail.acounts && this.detail.acounts <= this.detail.asalenum) {
      //   this.$toast("此活动礼包已售完");
      //   return;
      // }
      sessionStorage.setItem("active", JSON.stringify(this.detail));
      this.$router.push({
        name: "buyActivityGoods",
      });
    },
    shareClick() {
      if (this.detail.astatus == 1) {
        this.$toast("该活动还未开始");
        return;
      }
      if (this.detail.astatus == 2) {
        this.$toast("该活动已结束");
        return;
      }
      this.shareShow = true;
    },
    hideShareMask() {
      this.shareShow = false;
    },
    clickAudio() {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
        this.play = true;
      } else {
        this.$refs.audio.pause();
        this.play = false;
      }
    },
    loadData() {
      this.$toast.allowMultiple();
      let loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getShareActivity({
        acode: this.$route.query.id,
        parentCode: this.$route.query.parentId,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = res.data.Activity;
          this.detail.amsg = this.detail.amsg
            ? this.detail.amsg
            // .replace(
            //     /(<img[\s\S]+?)src=(['"])/g,
            //     '<img src="' + this.$config.http
            //   )
            : "";
          this.detail.aredMsgList = res.data.Activity
            ? res.data.Activity.aredMsg.split(/[\r\n]/)
            : [];
          if (this.detail.avisitnum > 0 && this.detail.avisitnum < 10000) {
            this.detail.avisitnumText = this.detail.avisitnum;
          } else if (
            this.detail.avisitnum >= 10000 &&
            this.detail.avisitnum < 100000
          ) {
            this.detail.avisitnumText =
              Math.floor(this.detail.avisitnum / 10000) + "万+";
          } else if (this.detail.avisitnum >= 100000) {
            this.detail.avisitnumText = "10万+";
          } else {
            this.detail.avisitnumText = "0";
          }

          this.shopDetail = res.data.scFrameUserShop;
          this.shopDetail.shopTitles = this.shopDetail.shopTitle.split(",");

          this.redPackList = res.data.RedPackList;
          this.redPacketetTotal = res.data.redPacketetTotal;
          let that = this,
            time =
              (new Date(this.detail.endDate.replace(/-/g, "/")).getTime() -
                new Date().getTime()) /
              1000;
          console.log(new Date(this.detail.endDate));
          if (time > 0) {
            this.Interval = setInterval(() => {
              if (time > 0) {
                time--;
                that.day = parseInt(time / 60 / 60 / 24);
                that.hour =
                  parseInt((time % (60 * 60 * 24)) / (60 * 60)) > 9
                    ? parseInt((time % (60 * 60 * 24)) / (60 * 60))
                    : "0" + parseInt((time % (60 * 60 * 24)) / (60 * 60));

                that.min =
                  parseInt((time / 60) % 60) > 9
                    ? parseInt((time / 60) % 60)
                    : "0" + parseInt((time / 60) % 60);
                that.sec =
                  parseInt(time % 60) > 9
                    ? parseInt(time % 60)
                    : "0" + parseInt(time % 60);
              } else {
                clearInterval(that.Interval);
                that.show = false;
                that.loadData();
                // that.router.back();
              }
            }, 1000);
          }
        } else {
          if (res.data) {
            this.detail = res.data;
            if (this.detail.avisitnum > 0 && this.detail.avisitnum < 10000) {
              this.detail.avisitnumText = this.detail.avisitnum;
            } else if (
              this.detail.avisitnum >= 10000 &&
              this.detail.avisitnum < 100000
            ) {
              this.detail.avisitnumText =
                Math.floor(this.detail.avisitnum / 10000) + "万+";
            } else if (this.detail.avisitnum >= 100000) {
              this.detail.avisitnumText = "10万+";
            } else {
              this.detail.avisitnumText = "0";
            }

            this.detail.aredMsgList = res.data.Activity
              ? res.data.Activity.aredMsg.split(/[\r\n]/)
              : [];
            this.redPackList = res.data.RedPackList;
          }
          this.$toast(res.msg);
        }
      });
    },
    choose(index) {
      this.active = index;
    },
    share() {
      let that = this;
      wxApi()
        .then(res => {
          console.log(this.$wx, res.data);
          this.$wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"],
          });
          this.$wx.ready(function() {
            that.$wx.onMenuShareAppMessage({
              title: that.detail.viewName, // 分享标题
              desc: that.detail.viewName2, // 分享描述
              link:
                that.$config.http +
                "/wx/#/shareActivity?parentId=" +
                sessionStorage.getItem("userId") +
                "&id=" +
                that.$route.query.id, // 分享链接
              imgUrl: that.$utils.dealImg(that.detail.aimg), // 分享图标
              type: "link", // 分享类型,music、video或link，不填默认为link
              dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
              success: function() {
                // 用户确认分享后执行的回调函数
                that.shareShow = false;
                that.show = true;
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
            that.$wx.onMenuShareTimeline({
              title: that.detail.viewName, // 分享标题
              link:
                that.$config.http +
                "/wx/#/shareActivity?parentId=" +
                sessionStorage.getItem("userId") +
                "&id=" +
                that.$route.query.id, // 分享链接
              imgUrl: that.$utils.dealImg(that.detail.aimg), // 分享图标
              success: function() {
                that.shareShow = false;
                that.show = true;
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
  },
  activated() {
    document.title = `【${
      this.$config.activeTitle
    }】一件爆款即将在您的见证下诞生`;
    this.loadData();
    this.share();
  },
  beforeDestroy() {
    clearInterval(this.Interval);
  },
};
</script>
<style lang="scss" scoped>
.activity {
  background: #f4f4f4;
  height: calc(100vh);
  .top {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    z-index: 9;
    &-content {
      line-height: 30px;
      font-size: 14px;
      text-align: center;
      background: rgba(0, 0, 0, 0.3);
      color: #fff;
    }
    .music {
      width: 26px;
      position: fixed;
      top: 2px;
      right: 10px;
      z-index: 2;
      animation: que 4s linear infinite;
    }
    @keyframes que {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
  }
  .mid {
    padding: 0 0 80px;
    .share {
      width: 70%;
      margin: 60px 0 0;
    }
    .title {
      width: 95%;
      margin: -10px 0 0;
    }
    &-top {
      margin: 0;
      .activeInfo {
        background: #fff;
        padding-bottom: 5px;
        img {
          width: 100vw;
          // height: calc(100vw / 1.316);
        }
        .flex {
          display: flex;
          align-items: center;
          padding: 5px 0;
          margin: 0 10px;
          .left {
            flex: 2;
            text-align: left;
            .symbol {
              font-size: 15px;
              color: #e12e24;
              transform: translateY(4px);
              display: inline-block;
            }
            .price {
              font-size: 22px;
              color: #e12e24;
              font-weight: bold;
              line-height: 30px;
              vertical-align: middle;
              margin-right: 3px;
            }
            .text {
              background: url("../../assets/img/activity/bg1.png") no-repeat;
              background-size: 100% 100%;
              font-size: 13px;
              line-height: 15px;
              padding: 5px 10px;
              border-radius: 5px;
            }
          }
          .right {
            width: 100px;
            text-align: right;
            font-size: 13px;
            color: #333;
            line-height: 20px;
          }
          .viewname {
            font-size: 13px;
            line-height: 20px;
            margin-right: 10px;
          }
          .remark {
            width: 140px;
          }
        }
        .borderTop {
          border-top: 1pt solid #e5e5e5;
        }
        .name {
          text-align: left;
          padding: 5px 10px 0;
          font-weight: bold;
          line-height: 30px;
        }
      }
      .time-wrap {
        color: #999;
        padding: 15px 0;
        background: #fff;
        text-align: center;
        margin: 10px 0 0;
        p {
          color: #000;
          font-size: 18px;
          font-weight: bold;
        }
        .time {
          display: flex;
          width: 150px;
          margin: 15px auto 0;
          .num {
            background: #fff;
            color: #000;
            width: 25px;
            border-radius: 5px;
            font-weight: bold;
            flex: 1;
            .value {
              font-size: 22px;
              line-height: 30px;
            }
            .label {
              font-size: 13px;
              font-weight: normal;
              color: #666;
              line-height: 20px;
            }
          }
          .point {
            color: #000;
            font-weight: bold;
            font-size: 22px;
            line-height: 25px;
          }
        }
      }
      .line-wrap {
        padding: 0 10px 40px;
        background: #fff;
        position: relative;
        .line {
          background: #f5aba7;
          width: 100%;
          height: 10px;
        }
        .showLine {
          position: absolute;
          left: 10px;
          top: 0;
          height: 10px;
          background: #e12e24;
        }
        .start {
          position: absolute;
          left: 10px;
          color: #666;
        }
        .end {
          position: absolute;
          right: 10px;
          color: #666;
        }
        .now {
          position: absolute;
          color: #666;
        }
      }
    }
    &-redPacket {
      padding: 20px 0 0;
      margin: 10px 0 0;
      background: #fff;
      .info {
        padding: 0 10px;
        border-bottom: 1pt solid #efefef;
        margin: 0 0 10px;
        .title {
          display: flex;
          align-items: center;
          p {
            font-size: 15px;
            color: #333;
            flex: 2;
            font-weight: bold;
            line-height: 25px;
          }
          img {
            width: 20px;
          }
        }

        .labels {
          margin: 10px 0 0;
          .label {
            display: inline-block;
            font-size: 13px;
            margin-right: 10px;
            background: #ffa500;
            padding: 3px 8px;
            border-radius: 10px;
            color: #fff;
          }
          .color {
            background: #ffeff2;
            color: #ef5852;
          }
        }
        .flex {
          display: flex;
          align-items: center;
          .list {
            flex: 2;
            padding: 10px 0;
            .item {
              display: flex;
              // align-items: center;
              font-size: 13px;
              line-height: 25px;
              .left {
                width: 80px;
              }
              .right {
                flex: 2;
              }
            }
          }
          .icon {
            width: 15px;
          }
        }
      }
      .classify {
        display: flex;
        align-items: center;
        padding: 0 10px;
        .classify-left {
          position: relative;
          width: 60%;
          img {
            width: 105%;
            height: 40px;
          }
          p {
            width: 100%;
            position: absolute;
            top: 0;
            line-height: 40px;
            text-align: center;
          }
        }
        .classify-right {
          width: 40%;
          position: relative;
          img {
            width: 105%;
            height: 40px;
            margin-left: -5%;
          }
          p {
            width: 100%;
            position: absolute;
            top: 0;
            line-height: 40px;
            text-align: center;
          }
        }
      }
      .redPacket-list {
        padding: 10px;
        .redPacket-item {
          display: flex;
          align-items: center;
          padding: 10px 0;
          border-bottom: 1pt solid #e5e5e5;
          .left {
            width: 45px;
            height: 45px;
            margin-right: 10px;
            img {
              width: 100%;
              border-radius: 50%;
            }
          }
          .name {
            flex: 2;
            text-align: left;
            p {
              line-height: 20px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              max-width: 50vw;
            }
          }
          .right {
            flex: 2;
            text-align: right;
            .price {
              color: #e3321e;
              font-size: 18px;
              line-height: 20px;
            }
          }
        }
      }
      .redPacket-rules {
        border-radius: 0 0 5px 5px;
        width: calc(90% - 20px);
        background: #fff;
        margin: 0 auto;
        padding: 10px;
        height: 283px;
        text-align: left;
        line-height: 25px;
      }
    }
